import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Login from "../views/LoginV.vue";
import About from "../views/about.vue";
import contact from "../views/contact.vue";
import services from "../views/services.vue";
import temp from "../views/test.vue";
import users from "../views/users.vue";
import excel from "../views/excel.vue";
Vue.use(VueRouter);

const routes = [
  {
    path: "/main",
    name: "Home",
    component: Home
  },
  {   path: "/",
  name: "Login",
  component: Login
  },
  {   path: "/about",
  name: "about",
  component: About
  },
  {   path: "/service",
  name: "service",
  component: services
  },
  {   path: "/contact",
  name: "contact",
  component: contact
  }
  ,
  {   path: "/temp",
  name: "temp",
  component: temp
  },
  {   path: "/users",
  name: "users",
  component: users
  },
  {   path: "/excel",
  name: "excel",
  component: excel
  }

  
];

const router = new VueRouter({
  mode: 'history', 
  routes
});

export default router;
