<template>
    <div>
             <section class="row" id="tmServices">
          
          
                
                                        
                       <v-row>
                           <v-col>
                          <template>
  <v-data-table
    :headers="headers"
    :items="desserts"
    sort-by="ser_desc"
    class="font-20 dir"
  >
    <template  v-slot:item.ser_type="{ item }" >
    <td v-if="item.ser_type==0">
       
    </td>
       <td v-else>
       
    </td>
  </template>
    <template v-slot:top >
      <v-toolbar
        flat
      >
        <v-toolbar-title></v-toolbar-title>
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
        <v-spacer></v-spacer>
        <v-dialog
          v-model="dialog"
          max-width="900px"

        >
             <v-form ref="add">
          <v-card >
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col
                 
                  
                  >
                    <v-text-field
 
     v-model="editedItem.ser_desc"
  persistent-hint
    label=" "
 
         append-icon="list"
         
          :rules="[() => !!editedItem.ser_desc ||'  ']"
            
            required
  solo

>
          </v-text-field>
            <v-row    class="justify-center">

            
 <v-radio-group
    v-model="editedItem.ser_type"
      row 
      required
      class="justify-evenly"
    >
         <v-radio
        label=" "
        :value="1"
            class="pr-5"
      ></v-radio>
      <v-radio 
        label=" "
        :value="0"
      
          class="pl-5"
       
      ></v-radio>
 
    </v-radio-group>
    
    
    </v-row>
             
               <v-row    class="justify-center">
      <v-col  cols="5"
        sm="5"
        md="5">
        
    <v-card elevation="10" cols="auto" class="dir"  :disabled="editedItem.ser_type=='1' ? true :false ">
  <v-card-title> </v-card-title>
   <v-card-text>
          <v-text-field cols="auto" class="dir"
             disabled
 label=""
            placeholder=" "
    
          ></v-text-field>
                <v-text-field cols="auto" class="dir"
                  disabled
            label=""
            placeholder=" "
        
          ></v-text-field>

                <v-text-field
         disabled
            label=""
            placeholder=" "
          ></v-text-field>

            <v-text-field cols="auto" class="dir"
            disabled
            label=""
            placeholder=" "
  
          ></v-text-field>
   </v-card-text>
  </v-card>
      </v-col>
      <v-col     cols="5"
        sm="5"
        md="5">
        
    <v-card elevation="10" cols="auto" class="dir"  :disabled="editedItem.ser_type=='0' ? true :false ">
  <v-card-title> </v-card-title>
     <v-card-text>
            <v-text-field cols="auto" class="dir"
             disabled
 label=""
            placeholder=" "
    
          ></v-text-field>

                <v-text-field
         disabled
            label=""
            placeholder=" "
          ></v-text-field>

                   <v-text-field
            disabled
            label=""
            placeholder=" "
          ></v-text-field>
   </v-card-text>
</v-card>
        </v-col>
      
      </v-row>
                    
                  </v-col>
                  
                  
                  
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                 color="Red"
  elevation="18"
  large
                @click="close"
              >
                
              </v-btn>
       
              <v-btn
               color="primary"
  elevation="18"
  large
                @click="EditService"
              >
                 
              </v-btn>
            </v-card-actions>
          </v-card>
             </v-form>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="headline">    </v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete"></v-btn>
              
              <v-btn color="blue darken-1" text @click="deleteItemConfirm"></v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template  v-if="this.$session.get('Group') && this.$session.get('Group')==1" v-slot:[`item.actions`]="{ item }">
      
      <v-btn rounded  class="ml-2"  color="primary" small   elevation="12"   @click="editItem(item)">
      <v-icon
       small
      >
        mdi-pencil
      </v-icon>
      </v-btn>
       <v-btn rounded  class="mr-2"  color="secondary" small   elevation="12"     @click="deleteItem(item)">
      <v-icon
        small
      >
        mdi-delete
      </v-icon>
       </v-btn>
    
  
    </template>
   <template v-else  v-slot:[`item.actions`]  >
<span>    </span>
    </template>
    
  </v-data-table>
</template>
                           </v-col>  
                       </v-row>
        </section>
    </div>
</template>

<script>
import axios from 'axios';
import router from "../router/index"
export default {
      data: () => ({
      search: '',
  
      dialog: false,
      dialogDelete: false,
      headers: [
        {
          text: ' ',
          align: 'start',
          sortable: false,
          value: 'ser_desc',
        },
        { text: ' ', value: 'ser_type' },
        
        { text: '', value: 'actions', sortable: false },
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
         id: 0,
        ser_desc: '',
        ser_type: 0,
        
      },
      defaultItem: {
        id: 0,
        ser_desc: '',
        ser_type: 0,
        
      },
      
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? ' ' : ''
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },

    beforeCreate(){
    if(!this.$session.get('token')){
        router.push('/Login');
      }
      },
    async created () {
  
        axios.get("http://127.0.0.1:8000/api/serAPI/serviceDisplay/")
    .then(res=>{
    
      this.desserts=res.data;
    }) .catch(err=>console.log(err));

    },

    methods: {
      editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        this.desserts.splice(this.editedIndex, 1)
         axios.delete(`http://127.0.0.1:8000/api/serAPI/serviceDisplay/${this.editedItem.id}/`).then(res=>{
      console.log(res)
    if(res.status==200){
      alert("Done");
    }else{
       alert("Error");
    }
        
    })
    .catch(err=>console.log(err));
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },EditService () {
        if (this.editedIndex > -1) {
            Object.assign(this.desserts[this.editedIndex], this.editedItem)
          //Edit Service
    axios.put(`http://127.0.0.1:8000/api/serAPI/serviceDisplay/${this.editedItem.id}/`,{ser_desc: this.editedItem.ser_desc,ser_type: parseInt(this.editedItem.ser_type)}).then(res=>{
      console.log(res)
    if(res.status==200){
      alert("Done");
    }else{
       alert("Erorr ");
    }
        
    })
    .catch(err=>console.log(err));
          
        } else {
          this.desserts.push(this.editedItem)
        }
        this.close()
      },
    }
} 

</script>
<style scoped>
.font-20{
  font-size: 20px!important;
}

</style>