<template>
 <div class="text-center ma-2">
  <v-snackbar v-model="show" :dark="false" :color="color" top>
   <span :color="color"> {{message}}</span>
   
      <template v-slot:action="{ attrs }">
        <v-btn
          color="accent"
          text
          v-bind="attrs"
        @click.native="show = false"
        >
        <v-icon color="white">close</v-icon>
        </v-btn>
      </template>
 
  </v-snackbar>
 </div>
</template>
<script>
  export default {
    data() {
      return {
        show: false,
        message: "",
        color: "",
        timeout: 3000
      };
    },
    created: function() {
      this.$store.watch(
        state => state.snack,
        () => {
          const message = this.$store.state.snack.message;
          if (message) {
            this.show = true;
            this.message = message;

            this.color = this.$store.state.snack.color;
            this.$store.commit("setSnack", {});
          }
        }
      );
    }
  };
</script>