<template>
    <div col="auto" class="dir">
     <template>
  <v-data-table
    :headers="headers"
    :items="desserts"
    sort-by="calories"
    class="elevation-1"
  >
   <template v-slot:item.is_active="{ item }">
  <v-checkbox
              v-model="item.is_active"
              color="indigo"
              hide-details
              disabled
            ></v-checkbox>
 </template>
    <template v-slot:top>
      <v-toolbar
        flat
      >
        <v-toolbar-title></v-toolbar-title>
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
        <v-spacer></v-spacer>
        <template>
  <v-row justify="center">
    <v-dialog
      v-model="dialog"
      persistent
      max-width="500px"
      class="tm-bg-white-transparent"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-btn
         color="blue"
          v-bind="attrs"
          v-on="on"
        >
          
        </v-btn>
      </template>
        <v-form ref="addUser">
      <v-card class="dir tm-bg-white-transparent">
        <v-card-title>
       <span class="headline">{{ formTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col
                cols="12"
              >
                <v-text-field
                  v-model="editedItem.username"
                  label="  (   )"
                  required
                  :rules="[rules.required]"
                   solo
                ></v-text-field>
              </v-col>
             
              <v-col
                cols="12"
              >
                <v-text-field
                 v-model="editedItem.first_name"
                  label="  (     )"
                  persistent-hint
                  :rules="[rules.required]"
                   solo
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label=""
                  required
                  v-model="editedItem.email"
                  :rules="[rules.required,rules.emailRules]"
                   solo
                ></v-text-field>
              </v-col>
                 <v-col cols="12">
                     <v-checkbox
       v-model="editedItem.is_active"
        label=" "
           solo
    ></v-checkbox>
           
              </v-col>
              <v-col cols="12">
                <v-text-field
                  v-model="password"
                  :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
              :rules="[rules.required]"
              :type="show ? 'text' : 'password'"
                  label="  (    )"
                  @click:append="show = !show"
                  required
                   solo
                ></v-text-field>
              </v-col>
                   <v-col cols="12">
                <v-text-field
                v-model="ConformPassword"
                :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
              :rules="[rules.required,rules.passwordRules]"
              :type="show1 ? 'text' : 'password'"
                  label="   (    )"
                @click:append="show1 = !show1"
                  required
                  solo
                ></v-text-field>
              </v-col>
              
            </v-row>
          </v-container>
        
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="blue darken-1"
            text
            @click="dialog = false"
          >
            Close
          </v-btn>
          <v-btn
            color="blue darken-1"
            text
            @click="save"
          >
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
        </v-form>
    </v-dialog>
  </v-row>
</template>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="headline">    </v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete"></v-btn>
              <v-btn color="blue darken-1" text @click="deleteItemConfirm"></v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:[`item.actions`]="{ item }">
       <v-btn rounded  class="ml-2"  color="primary" small   elevation="12"   @click="editItem(item)">
      <v-icon
       small
      >
        mdi-pencil
      </v-icon>
      </v-btn>
       <v-btn rounded  class="mr-2"  color="secondary" small   elevation="12"     @click="deleteItem(item)">
      <v-icon
        small
      >
        mdi-delete
      </v-icon>
       </v-btn>
    </template>
  
  </v-data-table>
</template>
                           

    </div>
</template>

<script>
import axios from 'axios';
import router from "../router/index"
export default {
     data() {
      return {
      search: '',
         show: false,
            show1: false,
            ConformPassword:null,
            password:null,
            
      dialog: false,
      dialogDelete: false,
      headers: [
        {
          text: ' ',
          align: 'start',
          sortable: false,
          value: 'first_name',
        },
        
        { text: ' ', value: 'username' },
        { text: '  ', value: 'is_active' },
          { text: '  ', value: 'email' },
        { text: '', value: 'actions', sortable: false },
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
        first_name: '',
        username: '',
        is_active:0,
        email:'',
        
      },
      defaultItem: {
          first_name: '',
        username: '',
        is_active:0,
        email:'',
        
      },
       rules: {
        required: value => !!value || 'Required.',
       passwordRules:confirmation => confirmation === this.password || 'Passwords must match',
      emailRules:v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'Invalid Email address' 
       },
      }
    },

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? ' ' : ''
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },
beforeCreate(){
    if(this.$session.get('Group')!=1 || !this.$session.get('token')){
        router.push('/main');
      }
}
    ,

    created () {
    
        axios.get("http://127.0.0.1:8000/api/serAPI/Users/")
    .then(res=>{
    console.log(res);
      this.desserts=res.data;
    }) .catch(err=>console.log(err));

    },

    methods: {
      editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
         this.desserts.splice(this.editedIndex, 1)
         axios.delete(`http://127.0.0.1:8000/api/serAPI/Users/${this.editedItem.id}/`).then(res=>{
      console.log(res)
    if(res.status==204){
      alert("Done");
    }else{
       alert("Error");
    }
        
    })
    .catch(err=>console.log(err));
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          alert("is working");
          if(this.editedItem.first_name!="" && this.editedItem.username!="" && this.editedItem.email !="" ){
            Object.assign(this.desserts[this.editedIndex], this.editedItem)
            var data={};
            if(this.password!="" && this.password!=null){
  
                data={username: this.editedItem.username,first_name: this.editedItem.first_name,email: this.editedItem.email,is_active: this.editedItem.is_active,password: this.password};
               axios.put(`http://127.0.0.1:8000/api/serAPI/Users/${this.editedItem.id}/`,data).then(res=>{
      console.log(res)
    if(res.status==200){
      alert("Done Editing");
    }else{
       alert("Erorr ");
    }
        
    })
    .catch(err=>console.log(err));
            
            }else{
              data={username: this.editedItem.username,first_name: this.editedItem.first_name,email: this.editedItem.email,is_active: this.editedItem.is_active};
            
          //Edit Service
    axios.put(`http://127.0.0.1:8000/api/serAPI/UserEd/${this.editedItem.id}/`,data).then(res=>{
      console.log(res)
    if(res.status==200){
      alert("Done Editing");
    }else{
       alert("Erorr ");
    }
        
    })
    .catch(err=>console.log(err));
          }
          }
        } else {
      if( this.$refs.addUser.validate()){
         this.desserts.push(this.editedItem)
       // alert(this.editedItem.first_name);
         axios.post(`http://127.0.0.1:8000/api/serAPI/Users/`,{username: this.editedItem.username,first_name: this.editedItem.first_name,email: this.editedItem.email,is_active: this.editedItem.is_active,password: this.password}).then(res=>{
      console.log(res)
    if(res.status==201){
      alert("Created");
      
    }else{
       alert("Erorr ");
    }
        
    })
    .catch(err=>console.log(err));
        }
        }
        this.close()
        
      },
    
    }
} 

</script>