<script>
import axios from 'axios';
import router from "../router/index"
export default {
data () {
      return {
        radios:null,
        ServiceDataArray:[],
             date:null,
      modal: false,
         hasSaved: false,
        isEditing: null,
        model: null,
       radioGroup:null,
       Day:null,
       Orgnazition:null,
       ServicesArray:[],
       itemsCheckbox:[" "," "," "],
    
          items: [],
      value: null,
     
         attributes:{
        value : null,
        writable : true,
        enumerable : true,
        configurable : true
      },
      menus:[],
      BillType:null,
      Totals:0.00,
      }
    },
    beforeCreate(){
    if(!this.$session.get('token')){
        router.push('/');
      }
},
  async created(){
   // alert(this.hasDecimal(10/3));
    //date
var days = ['', '', '', '', '', '', ''];
var today = new Date();
var dd = String(today.getDate()).padStart(2, '0');
var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
var yyyy = today.getFullYear();
this.date = mm + '/' + dd + '/' + yyyy;
var dayName = days[today.getDay()];
this.Day=dayName;
//Token for getting APIS
 console.log(this.$session.get('token'));
 console.log(this.$session.get('Group'));
//var to=this.$session.get('token');
//let tok={'Authorization': `Token ${to}`}
    // axios.get("http://127.0.0.1:8000/user/")
    // .then(res=>console.log(res))
    // .catch(err=>console.log(err));

//APIS for Getting Services

console.log(this.$session.get('username'))
console.log(this.$session.get('id'))
console.log(this.$session.get('Name'))


   axios.get("http://127.0.0.1:8000/api/serAPI/serviceDisplay/")
    .then(res=>{
    
      this.ServicesArray=res.data;
      //console.log(this.ServicesArray)
  

  this.ServicesArray.forEach(field => {
    this.menus.push({menu:false})
        var object={};
        Object.defineProperty(object, 'service_desc',{value :field.id,
        writable : true,
        enumerable : true,
        configurable : true});
        if(field.ser_type==1){
     Object.defineProperty(object, 'ser_name',this.attributes);
        Object.defineProperty(object, 'ser_type',this.attributes);
         Object.defineProperty(object, 'ser_number',this.attributes);
           Object.defineProperty(object, 'dateServics',this.attributes);
          Object.defineProperty(object, 'price',this.attributes);
        }else{
 Object.defineProperty(object, 'ser_name',this.attributes);
          Object.defineProperty(object, 'ser_number',this.attributes);
            Object.defineProperty(object, 'dateServics',this.attributes);
          Object.defineProperty(object, 'price',this.attributes);
        }
  this.ServiceDataArray.push(object);
  //console.log(this.menus);

        });
       //    console.log( this.ServiceDataArray);
    })
    .catch(err=>console.log(err));


//get Customers

    axios.get("http://127.0.0.1:8000/api/serAPI/customer/")
    .then(res=>{
      this.items=res.data;
      console.log(this.items);
    })
    .catch(err=>console.log(err));

  },
  methods:{
   hasDecimal (num) {
     // console.log(num%2);
     if(num%1==0){
       return num
     }else{
       return parseInt(num)+ 1;
     }

},CheckColumItem(len,elm){
 
 // console.log(elm);
  if(len-elm>=1){
    // console.log(3)
    return 3;
  }else if(len-elm<1){
   // console.log(len-elm+3)
    return len-elm+3;
  }
},async Saves(){
  var BigService=[];
  var SmallService=[];
  
   for (let i = 0; i < this.ServiceDataArray.length; i++) {
        if ( 'ser_type' in this.ServiceDataArray[i]) {
          if(this.ServiceDataArray[i].ser_name!==null && this.ServiceDataArray[i].ser_name!==""){
            this.ServiceDataArray[i].ser_number=parseInt(this.ServiceDataArray[i].ser_number);
             this.ServiceDataArray[i].price=parseFloat(this.ServiceDataArray[i].price);
          
           BigService.push(this.ServiceDataArray[i]);
          }
        }else{
             if(this.ServiceDataArray[i].ser_name!==null && this.ServiceDataArray[i].ser_name!=="" ){
            this.ServiceDataArray[i].ser_number=parseInt(this.ServiceDataArray[i].ser_number);
            this.ServiceDataArray[i].price=parseFloat(this.ServiceDataArray[i].price);

           SmallService.push(this.ServiceDataArray[i]);
             }
        }
    }
//Start Adding Service Selected
     var [BigSer,SmallSer]=await Promise.all([
      axios.post('http://127.0.0.1:8000/api/serAPI/BigService/',BigService),
        axios.post('http://127.0.0.1:8000/api/serAPI/SamllService/',SmallService)
      ])
      console.log(BigSer);
      if(BigSer.status==201 && SmallSer.status==201){
        BigService=BigSer.data
        SmallService=SmallSer.data
          var cus={};
        if(typeof this.value=="object"){
         cus=this.value;
        }else{
            //assigned Customer 
               cus=await axios.post('http://127.0.0.1:8000/api/serAPI/customer/',{name:this.value}).then(Sma=>{return Sma.data;}).catch(err=>console.log(err));

        }
          var CustomerBill={bill_number: 1,bill_type: parseInt(this.BillType),total_price: this.Totals,organzition_name:this.Orgnazition,cusotmer_id:cus.id,user_id:this.$session.get('id')}
          console.log(CustomerBill)
       
 axios.post('http://127.0.0.1:8000/api/serAPI/CustomerBill/',CustomerBill).then(res=>{
   if(res.status==201){
     let Bill_Id=res.data.id
     console.log(Bill_Id);
     var listBillitem=[];
     BigService.forEach(element => {
       listBillitem.push({big_service:element.id,customer_bill_id:Bill_Id});
     });
        SmallService.forEach(element => {
       listBillitem.push({small_service:element.id,customer_bill_id:Bill_Id});
     });
     axios.post('http://127.0.0.1:8000/api/serAPI/ServiceBookDetails/',listBillitem).then(res=>{
       console.log(res)
       if(res.status==201){
         alert("Done");
          location.reload();
       }

     })
    .catch(err=>console.log(err));
   }
    })
    .catch(err=>console.log(err));
     
     
      }

      
      console.log(SmallSer);

 
},Total(num){
  this.Totals+=parseFloat(num);
},async AddService(){
if(  this.$refs.add.validate()){

   
axios.post('http://127.0.0.1:8000/api/serAPI/serviceDisplay/', {ser_desc: this.ServiceNAme,ser_type: parseInt(this.radioGroup)}).then(res=>{
  
       if(res.status==201){
         alert("Create Secussfully");
  
            setTimeout(() => {
                          location.reload();
                }, 1000);
       }else{
         alert("try Again"); 
       }     })
    .catch(err=>console.log(err));
}else{
  alert("Nope");
}


}

  }
};
