<template>
<div>
    <v-btn @click="tableToExcel('table')">
Download Excel

    </v-btn>
 
  <table ref="table" id="loremTable" summary="lorem ipsum sit amet" rules="groups" frame="hsides" border="2">
    <caption> :{{Total}} </caption>
    <colgroup align="center"></colgroup>
    <colgroup align="left"></colgroup>
    <colgroup span="2" align="center"></colgroup>
    <colgroup span="3" align="center"></colgroup>
    <thead valign="top">
      <tr>
        <th> </th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
    </thead>
    <tbody>
<tr v-for="ser in Service" :key="ser.id">
<td>{{ser[0].ser_name}}</td>
<td>{{ser[0].ser_number}}</td>
<td>{{ser[0].price}}</td>
<td>{{ser[0].ser_number*ser[0].price}}</td>
</tr>
    </tbody>
   
  </table>
</div>
</template>


<script>
//http://127.0.0.1:8000/api/serAPI/CustomerBill/10/
import axios from 'axios';

  export default {
    data(){
        return{
        Service:[],
      desserts: [],
      Total:0,
        uri :'data:application/vnd.ms-excel;base64,',
      template:'<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40"><head><meta http-equiv="Content-Type" content="text/html;charset=UTF-8"><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets><x:ExcelWorksheet><x:Name>{worksheet}</x:Name><x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions></x:ExcelWorksheet></x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]--></head><body><table>{table}</table></body></html>',
      base64: function(s){ return window.btoa(unescape(encodeURIComponent(s))) },
      format: function(s, c) { return s.replace(/{(\w+)}/g, function(m, p) { return c[p]; }) },
    }
    },
     created () {
      axios.get("http://127.0.0.1:8000/api/serAPI/CustomerBill/10/")
    .then(res=>{
    console.log(res);
      this.desserts=res.data;
      this.Service=this.desserts.Servicelistname;
      console.log(this.Service)
this.Service.forEach(element => {
    this.Total+=element[0].ser_number*element[0].price;
});
    }) .catch(err=>console.log(err));
     },
     methods:{
    tableToExcel(table){
      
      if (!table.nodeType) table = this.$refs.table
      var ctx = {worksheet:" "+this.desserts.id || 'Worksheet', table: table.innerHTML}
      window.location.href = this.uri + this.base64(this.format(this.template, ctx))
    }
     }
  }
</script>