<template>
    <div col="auto" class="dir">
    <v-data-table
    :headers="headers"
    :items="desserts"
    item-key="name"
    group-by="id"
    class="elevation-1"
   :custom-sort="customSort"

  >
  <template v-slot:top  >
      <v-toolbar
        flat
      >
        <v-toolbar-title></v-toolbar-title>
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
        <v-spacer></v-spacer>
        <v-dialog
          v-model="dialog"
          max-width="900px"

        >
             <v-form ref="add">
          <v-card >
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
 <v-row    class="justify-center">
<v-col>
<v-card v-for="Servi in ServiceNames" :key="Servi[0].id" >

  <v-card-text class="row justify_ar">
    <span>
    {{Servi[0].ser_name}}


    </span>
              <v-btn
                 color="primary"
                 elevation="18"
                 large
                 rounded
                @click="Print(Servi[0])"
              >
               <v-icon>
                 print

               </v-icon>
              </v-btn>

             
  </v-card-text>
  <v-card-text>
       <span v-for="ser in SercisesNAMES" :key="ser.id">
         <h4  v-if="Servi[0].service_desc_id===ser.id">
            {{ser.ser_desc}} 
         </h4>
       </span>
  </v-card-text>

</v-card>
</v-col>   
   
 </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                 color="Red"
  elevation="18"
  large
                @click="close"
              >
                
              </v-btn>
       
          
            </v-card-actions>
          </v-card>
             </v-form>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="headline">    </v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete"></v-btn>
              
              <v-btn color="blue darken-1" text @click="deleteItemConfirm"></v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
<template v-slot:[`item.actions`]="{ item }">
   <v-btn
                class="ml-2"
                 color="green"
                 elevation="12"
                 small
                 rounded
                 @click="excel(item.id)">
                <v-icon>stacked_line_chart</v-icon>

              </v-btn>
      <v-btn rounded  class="ml-2"  color="primary" small   elevation="12"   @click="editItem(item)">
      <v-icon
       small
      >
     print
      </v-icon>
      </v-btn>
       <v-btn rounded  class="mr-2"  color="secondary" small   elevation="12"     @click="deleteItem(item)">
      <v-icon
        small
      >
        mdi-delete
      </v-icon>
       </v-btn>
    </template>
  </v-data-table>
  <table ref="table" id="loremTable" summary="lorem ipsum sit amet" rules="groups" frame="hsides" border="2" class="bg">
    <caption> :{{Total}} </caption>
    <colgroup align="center"></colgroup>
    <colgroup align="left"></colgroup>
    <colgroup span="2" align="center"></colgroup>
    <colgroup span="3" align="center"></colgroup>
    <thead valign="top">
      <tr>
        <th> </th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
    </thead>
    <tbody v-if="Service!=null && Service!=''">
    
<tr v-for="ser in Service" :key="ser.id">
<td>{{ser[0].ser_name}}</td>
<td>{{ser[0].ser_number}}</td>
<td>{{ser[0].price}}</td>
<td>{{ser[0].ser_number*ser[0].price}}</td>
</tr>
 
    </tbody>
   
  </table>  
   <div id="invoice" class="page" ref="invoicess" v-if="pdfdetails" >
        <p class="text-left">{{pdfdetails.billdate}}</p> 
       
        <div class="row">
        <div>  {{pdfdetails.service}}</div>  <div> </div> 
        
        </div>
        <br/>
             <br/>
<h4>    </h4>  
        <br/> 
      
        
  <h4>   -  </h4>     
           <br/> 
        <p>
                  ...   
              {{ pdfdetails.number}} 
              
                  {{pdfdetails.date}}
                  <br/>
             
                {{pdfdetails.customer}} 
           

        </p>
        <br/>
        
        <p class="text-right">      </p>
          <br/>
          <p>           </p>
    
        
        
       <p class="text-left"> </p> 
         <p class="text-left"> </p> 


        
  
    </div>                    

    </div>
</template>

<script>
import axios from 'axios';
import html2pdf from 'html2pdf.js'
export default {
      data(){
        return{
      search: '',
      dialog: false,
      dialogDelete: false,
      ServiceNames:[],
      headers: [
        {
          text: ' ',
          align: 'start',
          sortable: false,
          value: 'id',
        },
        { text: '  ', value: 'Customer' },
        { text: ' ', value: 'bill_date' },
        { text: '  ', value: 'Services' },
          { text: '  ', value: 'user' },
        { text: ' ', value: 'total_price' },
        
        { text: '', value: 'actions', sortable: false },
      ],
      desserts: [],
       Total:0,
       invoice:0,
        uri :'data:application/vnd.ms-excel;base64,',
        urlpdf:'data:application/pdf;base64,',
      template:'<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40"><head><meta http-equiv="Content-Type" content="text/html;charset=UTF-8"><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets><x:ExcelWorksheet><x:Name>{worksheet}</x:Name><x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions></x:ExcelWorksheet></x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]--></head><body><table>{table}</table></body></html>',
      base64: function(s){ return window.btoa(unescape(encodeURIComponent(s))) },
      format: function(s, c) { return s.replace(/{(\w+)}/g, function(m, p) { return c[p]; }) },
      SercisesNAMES:null,
      editedIndex: -1,
      editedItem: {
        Customer:'',
        id: 0,
        bill_date:'',
        Services: 0,
        total_price: 0,
        Service:null,
        user:''
        
      },
      defaultItem: {
        Customer:'',
        id: 0,
        bill_date:'',
        Services: 0,
        total_price: 0,
        user:''
      },
      inv:null,
      pdfdetails:null
    }
      },

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? ' ' : ' '
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },

    created () {
      axios.get("http://127.0.0.1:8000/api/serAPI/CustomerBill/")
    .then(res=>{
    console.log(res);
      this.desserts=res.data;
    }) .catch(err=>console.log(err));
 axios.get("http://127.0.0.1:8000/api/serAPI/serviceDisplay/")
    .then(res=>{
    console.log(res);
      this.SercisesNAMES=res.data;
    }) .catch(err=>console.log(err));

    },

    methods: {
      Print(Servi){
      

       console.log(Servi);
       this.pdfdetails={
         customer:this.inv.Customer,
         billdate:this.inv.bill_date,
         service:Servi.ser_name,
         number:Servi.ser_number,
         date:Servi.dateServics,
       }

//  var opt = {
//   margin:       1,
//   filename:     this.inv.Customer+this.inv.bill_date+".pdf",
//   image:        { type: 'jpeg', quality: 0.98 },
//   html2canvas:  { scale: 2 },
//   jsPDF:        { unit: 'in', format: 'letter', orientation: 'portrait' }
// };
     
          //  Html2Pdf(elm, opt);

          setTimeout(() => {
                     html2pdf(this.$refs.invoicess,{
            filename:'myfile.pdf',
  image:{ type: 'jpeg', quality: 0.98 },
            })
          }, 2000);
   
          

          
    

//  var opt = {
//   margin:       1,
//   filename:     'myfile.pdf',
//   image:        { type: 'jpeg', quality: 0.98 },
//   html2canvas:  { scale: 2 },
//   jsPDF:        { unit: 'in', format: 'letter', orientation: 'portrait' }
// };
 
// // New Promise-based usage:
// Html2Pdf().from(elm).save();
 
// // Old monolithic-style usage:
// Html2Pdf(elm, opt);
      },
        customSort(items, index, isDesc) {
        items.sort((a, b) => {
          if (isDesc != "false") {
            return a[index] < b[index] ? -1 : 1
          } else {
            return b[index] < a[index] ? -1 : 1
          }
        })
        return items
      }
      ,
     async excel(val){

          this.invoice=val;
             axios.get(`http://127.0.0.1:8000/api/serAPI/CustomerBill/${val}/`)
    .then(res=>{
    console.log(res);
      var all=res.data;
      this.Service=all.Servicelistname;
           this.Total=0;
this.Service.forEach(element => {
    this.Total+=element[0].ser_number*element[0].price;
  
});
   setTimeout(() => {
                    this.tableToExcel('table');
                }, 2000);

    }).catch(err=>console.log(err));
      },
      tableToExcel(table){
      
      if (!table.nodeType) table = this.$refs.table
      var ctx = {worksheet:" "+this.invoice || 'Worksheet', table: table.innerHTML}
      window.location.href = this.uri + this.base64(this.format(this.template, ctx))
    },
      editItem (item) {
        
        this.editedIndex = this.desserts.indexOf(item)
     this.ServiceNames=this.desserts[this.editedIndex].Servicelistname;
     this.inv=this.desserts[this.editedIndex];
     console.log(this.ServiceNames)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        this.desserts.splice(this.editedIndex, 1)
         axios.delete(`http://127.0.0.1:8000/api/serAPI/CustomerBill/${this.editedItem.id}/`).then(res=>{
      console.log(res)
    if(res.status==204){
      alert("Done");
    }else{
       alert("Error");
    }
        
    })
    .catch(err=>console.log(err));
        this.closeDelete()
      },

      close () {
        this.dialog = false
         this.pdfdetails=null
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.desserts[this.editedIndex], this.editedItem)
        } else {
          this.desserts.push(this.editedItem)
        }
        this.close()
      },
    }            
           
    
} 

</script>
<style scoped>
.justify_ar{
  justify-content: space-around!important;
      margin: 30px 0px 10px 0px;
}
.bg{
  display: none;
}
  .text-left{
      text-align: left;
    }
     .text-right{
      text-align: right;
    }
    .row{
        display: flex;
        justify-content: space-around;
    }
    .page{
            direction: rtl;
    margin: 35% 10% 0%;
    color: black!important;
    text-align: center;
         font-family: monospace;
        font-size: 20px;
       
    }

</style>